body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
}
body .fullwidth {
    width: 100%;
}
body .container {
    max-width: 1240px;
    margin: 0 auto;
    padding: 0 5%;
}
body .page-banner {
    width: 100%;
    margin: 0;
}
body .page-banner .banner-header {
      width: 100%;
      text-align: center;
}
body .header {
    font-size: 2em;
    padding: 0.67em 0;
    font-family: "Montserrat", sans-serif;
    color: #28112b;
}
body .button {
    border-radius: 5px;
    background-color: #f19c79;
    color: #fff;
    padding: 10px 20px;
    -webkit-transition: all 0.4s ease-out;
    transition: all 0.4s ease-out;
    cursor: pointer;
}
body .button:hover {
      background-color: #e7581d;
}
@media (max-width: 560px) {
body .desktop {
      display: none;
}
}
@media (min-width: 561px) {
body .mobile {
      display: none;
}
}
.slideDown-enter-active {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.slideDown-leave-active {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.slideDown-enter,
.slideDown-leave-to {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  opacity: 0;
}
.slideDownLong-enter-active {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.slideDownLong-leave-active {
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.slideDownLong-enter,
.slideDownLong-leave-to {
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
  opacity: 0;
}
.info-pop {
  width: 100%;
  height: 100vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  top: 0;
  color: #fff;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 9999;
  padding: 0 5%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
@media (min-width: 1241px) {
.info-pop {
      display: none;
}
}
.info-pop .close-pop {
    position: absolute;
    top: 0px;
    right: 25px;
    font-size: 5em;
    cursor: pointer;
}
.info-pop img {
    width: 50%;
    max-width: 300px;
}
@media (min-width: 561px) {
.info-pop img {
        width: 500px;
        max-width: unset;
}
}
